.char_parent{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-wrap: wrap;
    margin-top: 40px;
    justify-content: space-around;
    margin-left: auto;
    margin-right: auto;
    width: 360px;
}



.char_item{
    border: 1px solid orange;
    width: 110px;
    position: relative;
    margin-top: 10px;
    border-radius: 5px;
    box-sizing: border-box;
    cursor: grab;
    img{
        border-radius: 5px;
        width: 100%;
        height: 100%;
    }
    h4{
        position: absolute;
        font-weight: 500;
        bottom: 5px;
        left: 0;
        right: 0;
        text-align: center;
        padding: 0;
        margin: 0;
    }
}
