.popup{
    background-color: #21252B;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 330px;
    height: 170px;
    margin:auto;
    // z-index: 999;
    padding-top: 15px;

    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    div{
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        flex-direction: row;
        align-items: baseline;
        margin-top: 5px;
        padding-left: 50px;
        padding-top: 7px;
        padding-bottom: 7px;

        background-repeat: no-repeat;
        background-position: 10px;
        background-size: 30px;
    }
}


.modalWrapper{
    height: 100%;
    width: 100%;
    z-index: 99999;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0, .75);
    transition: 1s;
    display: none;
}

.modalWrapper.active{
    display: block;
}


.disable{
    display: none;
}
