.news_parent{
    display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      margin-top: 20px;
      align-items: center;
      padding-bottom: 80px;
}



.newsItem{
    max-width: 400px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 30px;
    p{
        margin-top: 7px;
        font-family: Roboto-Light, sans-serif;

        max-height: 72px;
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 1.2;
        padding-bottom: 5px;
    }
    img{
        margin-top: 7px;
        width: 100%;
        height: auto;
    }
}
