.settings{
    margin-top: 90px;
    margin-left: 10px;
    div{
        color: #EAAB02;
        margin-top: 20px;
    }
    ul{
        color: #fff;
    }
    li{
        margin-top: 20px;
    }
}

.onBtn{
    width: 300px;
    position: relative;
    span{
        position: absolute;
        right: 0;
        top: 5px;
        background-color: #949494;
        display: inline-block;
        width: 25px;
        height: 12px;
        border-radius: 10px;
        i{
            background-color: #fff;
            width: 16px;
            height: 16px;
            display: block;
            position: absolute;
            right: 12px;
            top: -2px;
            border-radius: 15px;
        }
    }
}


.spanActive{
    background-color: #A67A01;
}

.iActive{
    background-color: #EAAB02;
    right: -2px;
}
