.charMenu{
    // outline:1px solid red;
    max-width: 680px;
    display: flex;
    flex-direction: row;
    overflow: hidden;

}

.menu_item{
    white-space: nowrap;
    display: block;
    padding: 10px 13.5px;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 12px;
    color: #E9AA01;
}

.lineWrap{
  width: 360px;
}
.activeLine{
  height: 2px;
  width: 112px;
  background-color: orange;
}

.charPage{
    margin-top: 80px;
    max-width: 360px;
    margin:80px auto;
    box-sizing: border-box;
    outline: 1px solid white ;
}

.items{
    max-width: 360px;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    overflow: hidden;

}

.item{
    text-align: center;
    flex-shrink: 0;
    width: 360px;
    height: 500px;
    box-sizing: border-box;
    outline: 1px solid white ;
}


.slider-container .slider-items{
	transition: margin 1s ease;
}
