.login{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  width:100%;
  height: 100%;
  z-index: 999999;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper{
  text-align: center;
  background-color: #080C18;
  padding: 20px;
  width: 100%;
  max-width: 360px;
  border-radius: 5px;
  form{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 15px;
  }
  button{
    margin-bottom: 15px;
    padding: 5px 10px;
    background-color: #fff;
    outline: none;
    border-radius: 3px;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    color: black;
    &:hover{
        background: #fff;
    }
  }

}
