.header{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    background-color: #000;
    position: fixed;
    padding-top: 20px;
    padding-bottom: 20px;
    top: 0;
    width: 100%;
    z-index: 999;
    h1{
        font-size: 16px;
    }
}
.headerWrap{
    position: relative;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    width: 160px;
}





.menu{
    background-color: #000;
    padding: 0;
    margin: 0;
    list-style-type: none;
    position: fixed;
    bottom: 0;
    width: 100%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-around;
    padding-top: 35px;
    padding-bottom: 10px;
    a{
        padding-top: 25px;
        outline: none;
    }


}


.link{
    background-size: 20px;
    background-position: top;
    background-repeat: no-repeat;
}


.menu{
    .wfm_active{
            color: #EAAB02;
    }
}

///////////////////////////////////
@keyframes move_eye {
    0% {
        opacity: 0;
    } 75% {
        opacity: 1;
    }
}

@keyframes move_eye2 {
    0% {
        opacity: 0;
    } 75% {
        opacity: 1;
    }
}

@keyframes move_eye3 {
    0% {
        opacity: 0;
    } 75% {
        opacity: 1;
    }
}
@keyframes move_eye4 {
    0% {
        opacity: 0;
    } 75% {
        opacity: 1;
    }
}



.wrapperMenu{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    margin-left: 20px;
    overflow: hidden;
    width: 120px;
}

.header .titleName{
    font-size: 16px;
    display: block;
    margin-right: 60px;
    width: 90px;
}

.titleName_active{
    animation: move_eye 1s linear ;
}
.titleName_active2{
    animation: move_eye2 1s linear ;
}
.titleName_active3{
    animation: move_eye3 1s linear ;
}
.titleName_active4{
    animation: move_eye4 1s linear ;
}

.charTitle{
    display: block;
    width: 100px;
}

.goBack{
    font-weight: bolder;
    margin-right: 15px;
}


.titleChar{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
}




.menu_header{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-wrap:nowrap;
    margin-left: 0;
    transition: all 0.5s;
}

.menu_char{
    margin-left: -150px;
    animation: move_eye 1s linear;
}
