

body{
    background-color: #232732;
}

.body {
    color: #fff;
    font-family:Roboto-Regular, sans-serif;
    h1,h2,h3,h4,h5,h6{
        margin: 0;
        padding: 0;
    }
    a{
        color: #fff;
        text-decoration: none;
    }
    p{
            padding: 0;
            margin: 0;
    }
}

.disa{
    position: fixed;
    background-color: #fff;
    width: 500px;
    height: 500px;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
}
.disable{
    display: none;
}

.btn{
    width: 200px;
    height: 300px;
    position: absolute;
    bottom: 100px;
}



